import React, { Component } from 'react'
import Link from 'gatsby-link'
import Helmet from 'react-helmet'
import styled from 'styled-components'
import styles from '../styles'
import { rhythm } from '../utils/typography'
import { PageWrapper, PostContentWrapper } from '../components/content-wrapper'
import NavBar from '../components/NavBar'
import Footer from '../components/Footer'
import PageTitle from '../components/PageTitle'
import R from 'ramda'

type PostNode = {
  fields: {
    slug: string,
  },
  frontmatter: {
    title: string,
    date: string,
  },
}
type Edge = {
  node: PostNode,
}

type Props = {
  data: {
    allMarkdownRemark: {
      edges: Array<Edge>,
    },
    site: any,
  },
}

type PostByYear = Array<{
  year: string,
  posts: Array<PostNode>,
}>

const YearArchive = styled.ul`
  margin: ${rhythm(2)} 0;
  list-style-type: disc;
`

const YearTitle = styled.h2`
  position: relative;
  padding-left: 1rem;
  margin-top: ${rhythm(1)};
  margin-bottom: ${rhythm(1)};
  font-weight: 400;

  &::before {
    ${styles.mixins.centeredY};
    content: ' ';
    left: 0;
    width: 4px;
    height: 100%;
    background-color: #4568dc;
  }
`

const PostTitleLink = styled(Link)`
  position: relative;
  list-style: disc;
  display: block;
  margin: ${rhythm(0.5)} 0 ${rhythm(0.5)} 0;
  padding-left: 0.8rem;

  &::before {
    position: absolute;
    content: ' ';
    top: 0.65rem;
    left: 0;
    width: 0.2rem;
    height: 0.2rem;
    background-color: #4568dc;
    opacity: 0.6;
    border-radius: 1px;
  }

  &:hover {
    color: ${styles.colors.linkHover};
  }
`

class ArchiveRoute extends Component<Props> {
  postByYear: PostByYear = []

  // constructor(props: props) {
  //   super(props)
  // }

  makePostByYear() {
    const posts: Array<PostNode> = this.props.data.allMarkdownRemark.edges.map(
      (e: Edge) => e.node
    )
    const getYearFromDate = (year: string) => year.slice(0, 4)

    const parseDecimal = R.unary(parseInt)
    const sortDesc = (a, b) => parseDecimal(b) - parseDecimal(a)

    const allYears = R.pipe(
      R.map(p => p.frontmatter.date),
      R.map(getYearFromDate),
      R.uniq
    )(posts).sort(sortDesc)

    const yearlyPosts = (year: string, allPosts) => {
      return allPosts.filter(
        (post: PostNode) => year === getYearFromDate(post.frontmatter.date)
      )
    }

    this.postByYear = allYears.map(year => ({
      year,
      posts: yearlyPosts(year, posts).sort((pA: PostNode, pB: PostNode) => {
        const dateA = +new Date(pA.frontmatter.date)
        const dateB = +new Date(pB.frontmatter.date)
        return dateB - dateA
      }),
    }))
  }

  render() {
    const siteMetadata = this.props.data.site.siteMetadata
    const title = `연도별 포스트`
    const pageTitle = `${title} · ${siteMetadata.title}`
    this.makePostByYear()

    return (
      <PageWrapper>
        <Helmet
          title={pageTitle}
          link={[
            {
              rel: 'canonical',
              href: `${siteMetadata.url}${this.props.location.pathname}`,
            },
          ]}
          meta={[
            {
              property: 'og:url',
              content: `${siteMetadata.url}${this.props.location.pathname}`,
            },
            { property: 'og:title', content: title },
          ]}
        />
        <NavBar />
        <PostContentWrapper>
          <PageTitle>{title}</PageTitle>
          {this.postByYear.map(item => (
            <YearArchive key={item.year}>
              <YearTitle>{item.year}</YearTitle>
              {item.posts.map(post => (
                <PostTitleLink key={post.fields.slug} to={post.fields.slug}>
                  {post.frontmatter.title}
                </PostTitleLink>
              ))}
            </YearArchive>
          ))}
        </PostContentWrapper>
        <Footer />
        <Helmet
          description="연도별 포스트 목록"
          link={[
            {
              rel: 'canonical',
              href: `${siteMetadata.url}/archive`,
            },
          ]}
        />
      </PageWrapper>
    )
  }
}

export default ArchiveRoute

export const pageQuery = graphql`
  query ArchivePostBySlug {
    site {
      siteMetadata {
        title
        url
        author
        url
      }
    }
    allMarkdownRemark {
      edges {
        node {
          fields {
            slug
          }
          frontmatter {
            title
            date
            tags
          }
        }
      }
    }
  }
`



